.row {
    display: flex;   
    text-align: center;
    justify-content: center;
   }
   .letter-row {
       display: flex;
       align-items: center;
   }
    span {
       background-color: grey;
       color: white;
       font-weight: var(--font-bold);
       font-size: var(--font-md);
       margin: 0.25em;
       border-radius: 4px;
       padding: 0.25em;
       cursor: pointer;
   }